import { Component, OnInit, Inject } from '@angular/core';
import { CsJsonService } from '../cs-json.service';
import { Router, ActivatedRoute } from '@angular/router';
import { OwlOptions } from 'ngx-owl-carousel-o';

@Component({
  selector: 'app-case-studie',
  templateUrl: './case-studie.component.html',
  styleUrls: ['./case-studie.component.css']
})

export class CaseStudieComponent implements OnInit {


  customGalleryOptions: OwlOptions = {
    loop: true,
    mouseDrag: true,
    touchDrag: false,
    pullDrag: false,
    dots: false,
    navSpeed: 700,
    margin: 20,
    navText: ['<<', '>>'],
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 1
      },
      740: {
        items: 1
      },
      940: {
        items: 1
      }
    },
    nav: true
  }

  constructor(private csservice: CsJsonService, @Inject(Router)public router: Router, private activeRoute: ActivatedRoute) {
    this.router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };
   }
  
  csStudie: any = null;
  csStudieNext: any = null;
  csStudiePrev: any = null;
  ngOnInit(): void {
    //this.csservice.getCaseStudiesId(`slug=${this.route.snapshot.paramMap.get('slug')}`).subscribe(data => console.log(data['casestudies']));
    this.csservice.getCaseStudiesId().subscribe((data) =>
      (
      this.csStudie = data['casestudies'][this.activeRoute.snapshot.paramMap.get('slug')],
      this.csStudiePrev = data['casestudies'][this.activeRoute.snapshot.paramMap.get('slug')]['prev'],
      this.csStudieNext = data['casestudies'][this.activeRoute.snapshot.paramMap.get('slug')]['next']
      )
      );
  }

  gotopost(postid){
    //alert(postid);
   // this.router.navigate(['/casestudies/'+postid]);
    this.router.navigateByUrl('/casestudies/' + postid);
  
  }

}
