import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CsJsonService {

  constructor(private httpService: HttpClient) { }

  csStudies: any = null;

  getCaseStudies() {
    //console.log(query);
    return this.httpService.get('assets/json/casestudies.json');
  }

  getCaseStudiesId(query = null){
    //console.log(query);
    return this.httpService.get('assets/json/casestudies.json');
    }
}
